<template>
  <div id="app" class="container">
    <button @click="playRandomAudio">ランダム</button>
    <!--<img src="@/assets/makabe.webp" alt="Play Audio" @click="playRandomAudio" style="cursor: pointer;">-->
  </div>
</template>

<script>
export default {
  name: 'App',
  methods: {
    playRandomAudio() {
      const audioFiles = [
        'tgs/tgs1.mp3',
        'tgs/tgs2.mp3',
        'tgs/tgs3.mp3',
        'tgs/tgs4.mp3',
        'tgs/tgs5.mp3',
        'tgs/tgs6.mp3',
        'tgs/tgs7.mp3',
        'tgs/tgs8.mp3',
        'tgs/tgs9.mp3',
        'tgs/tgs10.mp3',
        'tgs/tgs11.mp3',
        'tgs/tgs12.mp3',
        'tgs/tgs13.mp3',
        'tgs/tgs14.mp3',
        'tgs/tgs15.mp3',
        'tgs/tgs16.mp3',
        'tgs/tgs17.mp3',
        'tgs/tgs18.mp3',
        'tgs/tgs19.mp3',
        'tgs/tgs20.mp3',
        'tgs/tgs21.mp3',
        'tgs/tgs22.mp3',
        // 他の音声ファイルへのパスをここに追加
      ];
      const randomIndex = Math.floor(Math.random() * audioFiles.length);
      const audio = new Audio(`${process.env.BASE_URL}${audioFiles[randomIndex]}`);
      audio.play();
    },
  },
};
</script>

<style>
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* ビューポートの高さ全体を使う */
}

